.card-dt {
    box-sizing: border-box;
    display: flex;
    background-image: url("../../resources/img/card.jpg");
    background-size:contain;
    display: flex;
    flex-wrap: wrap;
    width: 70%;
    height: 400px;
    margin: 10px;
    border: 2px solid rgb(168, 69, 11);
    padding: 10px;
    overflow: hidden;
}

.img-container-dt {
    display: flex;
    justify-content: center;
    overflow: hidden;
    min-width: 30%;
    height: 70%;
}

.photo-dt {
    object-fit: cover;
}

.card-title-dt {
    display: flex;
    width: 55%;
    font-size: 40px;
    text-shadow: 2px 2px 2px rgb(56, 25, 4);
    color: rgb(177, 80, 34);
    font-family: 'Dancing Script', cursive;
}

.card-text-container-dt {
    display: flex;
    width: 50%;
    flex-wrap: wrap;
    flex-direction: row;
    align-content: space-between;
    justify-content: center;
    align-items: flex-start;
}

.card-text-dt {
    display: flex;
    justify-content: left;
    margin-right: 20px;
    font-size: 20px;
    text-shadow: 3px 3px 3px rgb(255, 255, 255);
    font-weight: 500;
    margin-top: 5px;
}

.buttons-recipe-card-dt {
    display: flex;
    margin: 10px;
    justify-content:last space-between;
}

.corner-left-dt {
    width: 60px;
    padding: 5px;
    border: 1px solid #ffd6c3;
    color: rgb(255, 255, 255);
    font-size: 12px;
    border-radius: 5px;
    text-decoration: none;
    font-family:'Roboto',sans-serif;
    font-weight: 600;
    background-color: #a152119f;
    text-align: center;
    margin: 0 20px;
}

.corner-left-dt:hover {
    background-color: #45b818;
}

.corner-right-dt {
    width: 60px;
    padding: 5px;
    border: 1px solid #ffd6c3;
    color: rgb(255, 255, 255);
    font-size: 12px;
    border-radius: 5px;
    text-decoration: none;
    font-family:'Roboto',sans-serif;
    font-weight: 600;
    background-color: #a152119f;
    text-align: center;
    margin: 0 20px;
}

.corner-right-dt:hover {
    background-color: #e41919;
}
