.card {
    background-image: url("../../resources/img/card.jpg");
    background-size: contain;
    display: flex;
    width: 200px;
    height: 400px;
    margin: 10px;
    border: 2px solid rgb(110, 58, 16);
    flex-direction: column;
    justify-content:flex-start;
    align-content:center;
}

.img-container {
    overflow: hidden;
    width: 200px;
    height: 200px;
}

.photo,
.photo > img {
    max-width: 200px;
    object-fit: contain;
}

.card-title {
    font-size: 22px;
    text-shadow: 2px 2px 2px rgb(56, 25, 4);
    color: rgb(177, 80, 34);
    font-family: 'Dancing Script', cursive;
    text-align: center;
}

.card-text-container {
    line-height: inherit;
    justify-content: center;
    display: inline-flex;
    flex-wrap: wrap;
}

.card-text {
    font-size: 20px;
    line-height: inherit;
    text-shadow: 3px 3px 3px rgb(255, 255, 255);
    font-weight: 500;
    margin: 10px;
    text-align: center;
}

.buttons-recipe-card {
    display: inline-flex;
    justify-content: space-around;
}

.corner-left{
    width: 60px;
    padding: 5px;
    border: 1px solid #ffd6c3;
    color: rgb(255, 255, 255);
    font-size: 12px;
    border-radius: 5px;
    text-decoration: none;
    font-family:'Roboto',sans-serif;
    font-weight: 600;
    background-color: #a152119f;
    text-align: center;
}

.corner-left:hover {
    background-color: #45b818;
}

.corner-right{
    width: 60px;
    padding: 5px;
    border: 1px solid #ffd6c3;
    color: rgb(255, 255, 255);
    font-size: 12px;
    border-radius: 5px;
    text-decoration: none;
    font-family:'Roboto',sans-serif;
    font-weight: 600;
    background-color: #a152119f;
    text-align: center;
}

.corner-right:hover {
    background-color: #e41919;
}
